body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'epilogue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    overflow-x: hidden;
    min-width: 320px;
  padding: 0;
    box-sizing: border-box;
    width: 100vw;
}

@font-face {
    font-family: 'epilogue';
    src: local('epilogue'), url(./fonts/Epilogue-VariableFont_wght.ttf) format('truetype');
}

ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

a {
    transition: all .25s ease;
}
