.containerTop {
    display: flex;
    justify-content: space-between;
    padding: 16px 0;
    width: 85%;
    max-width: 1400px;
    margin: 0 auto;
}

.logo {
    font-weight: bold;
    font-size: 24px;
    padding-left: 59px;
    background: url("./img/logo.png") left/contain no-repeat ;
    line-height: 46px;
}

.navContainer{
    display: flex;
    flex: 0 1 50%;
    justify-content: space-between;
    align-items: center;
}

.navContainer a:hover{
    color: #EF6D58;
}

.contact{
    font-size: 14px;
    line-height: 46px;
    border: 1px solid rgba(255,255,255,0.29411764705882354 );
    flex: 0 0 140px;
    text-align: center;
    border-radius: 7px;
    box-shadow: rgba(255, 255, 255, 0.3) 0px 0px 4px;
}

.contact:hover {
    box-shadow: rgba(255, 255, 255, 0.3) 0px 0px 7px;
}

.wrapper {
    background: #28293E;

}

.containerTitle {
    margin: 0 auto;
    text-align: center;
    width: 85%;
    max-width: 1200px;
}

h1 {
    font-size: 60px;
    font-weight: normal;
    color: #fff;
    line-height: 150px;
}

p {
    color: rgba(255,255,255,0.6392156862745098 );
    padding-bottom: 75px;
    margin: 0;
}

.containerTop a{
    text-decoration: none;
    color: #fff;
    cursor: pointer;
}

