.itemsContainer {
    display: flex;
    justify-content: space-between;
    padding: 16px 0;
    max-width: 1600px;
    margin: 0 auto;
    flex-wrap: wrap;
    width: 85%;
}

.selector {
    border: 1px solid #ef6d58;
    border-radius: 3px;
    color: #ef6d58;
    height: 40px;
    width: 70%;
    margin: 30px 0 50px;
}

.wrapper {
    border: 1px solid #65DA69;
    justify-content: center;
    display: flex;
    flex-direction: column;
    padding: 0 10px;
}

.filterContainer {
    display: flex;
    flex: 1 0 70%;
    margin: 0 auto;
    min-width: 900px;
    flex-direction: row;
    justify-content: space-between;
    padding: 70px 0;
    max-width: 1200px;
}

.filterContainer li {
    text-align: center;
    flex: 1 0 20%;

}

.filter {
    cursor: pointer;
    font-size: 1rem;
    color: rgba(57,20,0,0.6392156862745098 );

}

.filter:hover {
    color: rgba(101, 218, 105, 0.5);
}

.filterActive {
    color: #65DA69;
}

.wrapper {
    background: #FDF0E9;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.category{
    font-size: 16px;
    color: #391400;
    line-height: 40px;
    padding: 0 20px;
    background-color: #fff;
    border-radius: 20px;
    text-align: center;
    margin-bottom: 45px;
    cursor: pointer;
    user-select: none;
}


.title {
    font-size: 40px;
    color: #fff;
    user-select: none;
}

.loadMore {
    font-size: 14px;
    line-height: 50px;
    padding: 0 50px;
    color: #391400;
    flex: 0 0;
    border: none;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    margin: 60px auto;
    cursor: pointer;
    border-radius: 7px;
    transition: all .25s ease;
    user-select: none;
}
.loadMore:hover {
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 12px;
}
.disabled {
    opacity: .7;
    box-shadow: none;
    border: 1px solid rgba(0, 0, 0, 0.14);

    cursor: default;
}

.disabled:hover {
    box-shadow: none;
}
